ul.timeline {
    list-style-type: none;
    position: relative;
    max-height: 500px;
    overflow-y: scroll;
}

ul.timeline:before {
    content: ' ';
    background: #d4d9df;
    display: inline-block;
    position: absolute;
    left: 29px;
    width: 2px;
    height: 100%;
    z-index: 400;
}

ul.timeline>li {
    margin: 20px 0;
    padding-left: 20px;
}

ul.timeline>li:before {
    content: ' ';
    background: white;
    display: inline-block;
    position: absolute;
    border-radius: 50%;
    border: 3px solid #f01470;
    left: 20px;
    width: 20px;
    height: 20px;
    z-index: 400;
}

.card{
    margin-left:60px;
    width:-webkit-fill-available;
    height: auto;
}
