.container {
  margin-top: 50px;
  text-align: center;
}

.image-grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-auto-rows: minmax(50px, auto);  
}

img{
  display: flex;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-item:nth-child(5n){
  grid-column-end: span 2;
}



/* For Video Gallery : */
.video-grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-auto-rows: minmax(50px, auto);  
}

.video_player{
  display: flex;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

