/* Menu Dropdown */
.mega_menu_nav.nav > li > div {
	position: absolute;
	padding:40px;
	display: block;
	width: 100%;
	top: 60px;
	left: 0;
	box-shadow: 2px 2px #E0E0E0;
	opacity: 0;
	visibility: hidden;
	overflow: hidden;

	background: white;
	border-radius: 0 0 3px 3px;

	-webkit-transition: all .3s ease .15s;
	transition: all .3s ease .15s;
}

.mega_menu_nav.nav > li:hover > div {
	opacity: 1;
	visibility: visible;
	overflow: visible;
}

